import React from 'react'
import Pdf from './files/Charles_Curtis_Resume.pdf'

const Resume = () => {
  return (
    <div className="resume container">
      <h3>Resume</h3>
      <div>
        <a className="btn btn-lg btn-success" href={Pdf} download="CharlesCurtisResume">View/Download Resume</a>
      </div>
    </div>
  )
}

export default Resume



