import React from 'react'
import TextLoop from 'react-text-loop'

const Home = () => {
  return (
    <div className='home'>
      <h3 className='top'>Software Engineer</h3>
      <TextLoop className='loop'>
        <h3>Problem Solver</h3>
        <h3>Growth Advocate</h3>
        <h3>Ski Fanatic</h3>
        <h3>Web Developer</h3>
      </TextLoop>
    </div>
  )
}

export default Home



