import React from 'react'
import ProfilePic from './files/round-profile.png'

const About = () => {
  return (
    <div className="about container">
      <div className="row">
        <div className="col-12">
          <img src={ProfilePic} alt="Charles Curtis"/>
          <p className="about-me">
              Web development combines my love of
              learning with the satisfaction of creating products
              that serve a purpose.  My experience working with a variety of
              high-performing teams mean that I thrive in a collaborative setting.
              From the careful planning and risk-management that it takes to
              be a ski instructor, to the adaptability and multi-tasking
              capacities required by customer success and service roles, my diverse
              background and ability to adapt give me the edge in
              modern web development.
          </p>
        </div>
      </div>
      <h3 className="tech">Technologies</h3>
      <div className="row icons mt-2">
        <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
          <i className="devicon-atom-original-wordmark m-2"></i>
          <i className="devicon-bootstrap-plain-wordmark m-2"></i>
          <i className="devicon-css3-plain-wordmark m-2"></i>
          <i className="devicon-express-original m-2"></i>
        </div>
        <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
          <i className="devicon-git-plain-wordmark m-2"></i>
          <i className="devicon-github-plain m-2"></i>
          <i className="devicon-heroku-plain-wordmark m-2"></i>
          <i className="devicon-html5-plain-wordmark m-2"></i>
        </div>
        <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
          <i className="devicon-javascript-plain m-2"></i>
          <i className="devicon-jquery-plain-wordmark m-2"></i>
          <i className="devicon-mongodb-plain-wordmark m-2"></i>
          <i className="devicon-nodejs-plain-wordmark m-2"></i>
        </div>
        <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
          <i className="devicon-rails-plain-wordmark m-2"></i>
          <i className="devicon-react-original m-2"></i>
          <i className="devicon-ruby-plain m-2"></i>
          <i className="devicon-sass-original m-2"></i>
        </div>
      </div>
    </div>
  )
}

export default About



